html {
  font-size: 62.5%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.body-fixed {
  overflow: hidden;
}
body.bodyScroll {
  padding-top: 12.3rem;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
  color: #000000;
}

a {
  text-decoration: none;
}

video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  video {
    width: 100%;
  }
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.container {
  width: min(95%, 120rem);
  margin: 0 auto;
}

.header {
  background-color: #4CB8B3;
  padding: 3rem 0;
  left: 0;
  top: 0;
}
@media (min-width: 768px) {
  .header.fixedBar {
    position: fixed;
    width: 100%;
    box-shadow: 0.2rem 0.2rem 0.5rem black;
    padding: 2rem 0;
    z-index: 10;
  }
}
.header h1 {
  font-size: 3.5rem;
  color: #ffffff;
  margin-bottom: 0;
  text-align: left;
}
@media (min-width: 480px) {
  .header h1 {
    text-align: center;
    font-size: 4rem;
  }
}
@media (min-width: 768px) {
  .header h1 {
    text-align: left;
    font-size: 4.5rem;
  }
}
@media (min-width: 992px) {
  .header h1 {
    text-align: left;
    font-size: 5rem;
  }
}
@media (min-width: 1200px) {
  .header h1 {
    text-align: left;
    font-size: 5.5rem;
  }
}
.header h1 .w1, .header h1 .m {
  color: #F53756;
}
.header h1 .punto, .header h1 .w2 {
  color: #FEF46D;
}

.main-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
}
@media (min-width: 992px) {
  .main-nav {
    flex-direction: row;
    align-items: center;
  }
}
.main-nav a {
  display: inline-block;
  color: #ffffff;
  font-size: 2rem;
  margin-inline: 1rem;
  text-align: left;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 480px) {
  .main-nav a {
    text-align: center;
    font-size: 2.4rem;
  }
}
@media (min-width: 768px) {
  .main-nav a {
    text-align: left;
  }
}
.main-nav a:hover {
  color: #FEF46D;
  font-size: 2.6rem;
  transform: scale(1.05);
}

.header-container {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 480px) {
  .header-container {
    flex-direction: column;
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .header-container {
    flex-direction: row;
  }
}

.hero {
  position: relative;
  max-height: 60vh;
  overflow: hidden;
  min-height: 50rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 480px) {
  .hero {
    max-height: 70vh;
  }
}
@media (min-width: 768px) {
  .hero {
    max-height: 80vh;
  }
}
@media (min-width: 992px) {
  .hero {
    max-height: 90vh;
  }
}
@media (min-width: 1200px) {
  .hero {
    max-height: 100vh;
  }
}
.hero .overlay {
  position: absolute;
  background-image: linear-gradient(19deg, rgba(33, 213, 253, 0.4980392157) 0%, rgba(185, 33, 255, 0.4392156863) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
}

.videoText-container h2 {
  margin: 0;
  font-size: 3.5rem;
  text-shadow: 2px 1.5px 5px rgba(0, 0, 0, 0.342);
  color: #ffffff;
}
@media (min-width: 480px) {
  .videoText-container h2 {
    font-size: 4rem;
  }
}
@media (min-width: 768px) {
  .videoText-container h2 {
    font-size: 5rem;
  }
}
@media (min-width: 992px) {
  .videoText-container h2 {
    font-size: 7rem;
  }
}
.videoText-container p {
  color: #ffffff;
  text-shadow: 2px 1.5px 5px rgba(0, 0, 0, 0.404);
  display: inline-block;
  padding: 0.5rem 2.5rem;
  background-color: #F53756;
  margin: 0.5rem 0;
}
@media (min-width: 480px) {
  .videoText-container p {
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .videoText-container p {
    font-size: 2.4rem;
  }
}
@media (min-width: 992px) {
  .videoText-container p {
    font-size: 3rem;
  }
}

.concerts-about {
  padding: 5rem 0;
}
@media (min-width: 992px) {
  .concerts-about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.concerts-about-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 2rem;
}
@media (min-width: 992px) {
  .concerts-about-text {
    margin-top: 0;
  }
}
.concerts-about-text h2 {
  margin-bottom: 1rem;
}
.concerts-about-text .fecha {
  margin-bottom: 1rem;
  color: #F53756;
  font-weight: bold;
}
.concerts-about-text .text {
  line-height: 1.4;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
  font-weight: bold;
}

@media (min-width: 768px) {
  .stage-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.stage-container p {
  color: #000000;
}

.stage {
  padding-top: 2rem;
}
.stage ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.stage li {
  padding: 2.5rem;
  font-weight: bold;
  display: block;
}
.stage li span {
  font-weight: normal;
}
.stage .stage-name:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.rock .stage-name:after {
  background-image: url(../img/icono_rock.png);
}

.edm .stage-name:after {
  background-image: url(../img/icono_edm.png);
}

.bg-yellow {
  background-color: #FEF46D;
}
.bg-yellow li:nth-child(2n+1) {
  background-color: #fdee21;
}

.bg-green {
  background-color: #4CB8B3;
}
.bg-green li:nth-child(2n+1) {
  background-color: #348380;
}

.gallery-container {
  padding: 5rem 0;
}
.gallery-container h3 {
  text-align: center;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 12;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.469);
}
.overlay picture {
  display: block;
  max-width: 75rem;
  z-index: 18;
}

.close-button {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.549);
  color: white;
  display: grid;
  place-items: center;
  font-weight: bold;
  font-size: 3.5rem;
  cursor: pointer;
}

.tickets-container {
  padding: 5rem 0;
}
.tickets-container h3 {
  text-align: center;
  color: #ffffff;
}

@media (min-width: 768px) {
  .tickets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.tickets .ticket-name {
  color: #ffffff;
  font-size: 2.5rem;
  text-align: center;
  font-weight: bold;
}

.pass-header, .pass-footer {
  padding: 0.35rem;
}
.pass-header p, .pass-footer p {
  text-align: center;
  font-size: 2.2rem;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
}
.pass-header {
  background-color: #F53756;
}
.pass-body {
  background-color: #ffffff;
}
.pass-body .options {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass-body .options li {
  background-color: unset;
  padding: 1rem;
  font-size: 1.8rem;
}
.pass-footer {
  background-color: #752F97;
}

.footer {
  background-color: #F53756;
  padding: 3rem;
}
.footer p {
  margin: 0;
  text-align: center;
  font-size: 2.2rem;
  color: #ffffff;
}